body {
  background-color: #6b9fe8;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  text-align: center;
  margin: 0;
  padding: 0 64px;
  font-family: Montserrat, sans-serif;
  font-size: 48px;
  font-weight: 200;
  line-height: 59px;
}

.stopwatch {
  grid-row-gap: 23px;
  width: 100%;
  justify-items: center;
  margin-top: 10%;
  padding-top: 25px;
  display: grid;
}

.time {
  background-color: #fff;
  border: 5px solid #fff;
  border-width: 5px 40px;
  border-radius: 25px;
  font-family: Roboto Mono, monospace;
  font-size: 48px;
  font-weight: 300;
}

.controls {
  width: 187px;
  justify-content: space-between;
  display: flex;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

.timelabel {
  color: #fff;
  text-align: center;
}

#playButton {
  display: block;
}

#pauseButton {
  display: none;
}

label.logo {
  color: #fff;
  float: left;
  padding-top: 1%;
  padding-left: 1%;
  font-size: 35px;
}

.topnav {
  width: 100%;
  box-sizing: border-box;
  background-color: #444;
  text-decoration: none;
  overflow: hidden;
}

.topnav ul {
  float: right;
}

.topnav ul li {
  text-align: center;
  padding: 14px 16px;
  font-size: 17px;
  display: inline-block;
}

.topnav ul li a {
  color: #2d2d2d;
  background-color: #fff;
  border: 5px solid #fff;
  border-width: 5px 25px;
  border-radius: 25px;
  padding: 10px;
  text-decoration: none;
}

.topnav a:hover {
  color: #2d2d2d;
  background-color: #838383;
  border-color: #838383;
  text-decoration: underline;
}

.topnav a.active {
  font-weight: bold;
  text-decoration: underline;
}

.topnav .icon {
  display: none;
}

@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {
    display: none;
  }

  .topnav a.icon {
    float: right;
    background-color: #60786f;
    display: block;
    transform: scale(1.3, 2.43);
  }
}

@media screen and (max-width: 600px) {
  .topnav.responsive {
    position: relative;
  }

  .topnav.responsive .icon {
    position: absolute;
    top: 0;
    right: 0;
  }

  .topnav.responsive a {
    float: none;
    text-align: left;
    display: block;
  }
}

#task-grid {
  grid-template: "taskform tasklist" 1fr
  / 1fr 1fr;
  display: grid;
}

#taskform {
  max-width: 300px;
  grid-area: taskform;
  margin: 0 auto;
}

#taskform label {
  width: 100%;
  display: block;
}

#tasklist {
  grid-area: tasklist;
  list-style-type: none;
}

